* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --blue-bg: #173f5f;
  --red-bg: #ed563be1;
  --white: rgba(255, 255, 255, 0.8);
}

.game-container {
  position: relative;
  width: 100vw;
  max-width: 700px;
  height: 510px;
  text-align: center;
  /* border-radius: 30px; */
  background: var(--blue-bg);
}

.menu {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* border-radius: 30px; */
  background-color: var(--white);
}

.fade-in {
  opacity: 1;
  z-index: 2;
  animation: fade 2s linear;
}

.fade-out {
  opacity: 0;
  /* z-index: -1; */
  animation: fade-out 1s linear;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.title {
  display: flex;
  justify-content: center;
  background-color: var(--red-bg);
  padding: 2em 2em;
  color: var(--blue-bg);
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* border-top-left-radius: 50px; */
  /* border-top-right-radius: 50px; */
  /* border-radius: 30px; */
}

.title-words {
  font-size: 2.7rem;
}

.choose-difficulty {
  padding: 1em;
  color: var(--blue-bg);
}

.difficulty-inputs {
  display: flex;
  flex-direction: column;
}

.difficulties {
  cursor: pointer;
  width: 120px;
  height: 33px;
  margin: 5px auto;
  font-size: 1.1rem;
  padding: 0.3em;
  color: var(--red-bg);
  background-color: var(--white);
  outline: none;
  border: 0;
  
}

.selectedDifficulty {
  border: 2px solid var(--blue-bg);
}

.difficulties:disabled {
  opacity: 0.4;
  cursor: not-allowed;
  border: 0;
}

.current-difficulty {
  color: var(--blue-bg);
  margin-top: 20px;
  font-size: 1.2rem;
}

.current-difficulty span {
  display: block;
  color: var(--red-bg);
}

.start-btn-area {
  background-color: var(--red-bg);
  margin-top: auto;
  padding: 2em 0;
  /* border-bottom-left-radius: 50px; */
  /* border-bottom-right-radius: 50px; */
}

.start-game {
  padding: 0.4em 1em;
  font-size: 1.5rem;
  background-color: var(--white);
  color: var(--blue-bg);
  cursor: pointer;
  border: 0;
}

.start-game:hover {
  background-color: var(--blue-bg);
  color: var(--white);
}

@media(min-width: 500px) {
  .game-container {
    width: 80vw;
    border-radius: 30px;
  }
  .menu {
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
  }
  .title {
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
  }
}

.game {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0 2em;
  background-color: var(--white);
  border-radius: 30px;
}

.turn {
  width: fit-content;
  margin: 0.5em auto;
  padding: 0 0 2em 0;
}

.board-container {
  padding: 0.2em 0;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 300px;
  margin: 0 auto;
}

.score-board {
  display: flex;
  padding: 2em 0;
  justify-content: space-between;
}

.score {
  background-color: var(--red-bg);
  padding: 0.2em;
  /* color: var(--red-bg); */
}

.cell {
  border: 1px solid black;
  font-size: 5rem;
  width: 100px;
  height: 90px;
  color: var(--blue-bg);
  display: flex;
  justify-content: center;
  align-items: center;
}



.cell:nth-child(-n+3) {
  border-top: 0;
}

.cell:nth-child(3n-2) {
  border-left: 0;
}

.cell:nth-child(3n-6) {
  border-right: 0;
}

.cell:nth-child(n+7) {
  border-bottom: 0;
}

.x-cell {
  height: 60px;
  width: 60px;
  position: relative;
  overflow: hidden;
}

.o-cell {
  width: 60px;
  height: 62px;
  display: flex;
}

.x-left,
.x-right {
  width: 70px;
  height: 4px;
  position: absolute;
  top: 28px;
  left: -4px;
  border-radius: 20px;
  background-color: var(--blue-bg);
}

.x-left {
  transform: rotate(45deg);
  animation-name: left;
  animation-duration: 0.4s;
}

.x-right {
  transform: rotate(-45deg);
  animation-name: right;
  animation-duration: 0.4s;
}

.circle {
  fill: transparent;
  stroke: var(--red-bg);
  stroke-width: 4;
  stroke-dasharray: 250;
  stroke-dashoffset: 1000;
  animation: rotate 0.4s linear;
}

@keyframes left {
  0% {
    top: -30px;
    left: -60px;
  }
  100% {
    top: 28;
    left: -4px;
  }
}

@keyframes right {
  0% {
    top: -50px;
    left: 45px;
  }
  40% {
    top: -30px;
    left: 45px;
  }
  100% {
    top: 28px;
    left: -4px;
  }
}

@keyframes rotate {
  from {
    stroke-dashoffset: 250;
  }
  to {
    stroke-dashoffset: 0;
  }
}

.x-turn-hover,
.o-turn-hover {
  cursor: pointer;
}

.x-turn-hover {
  position: relative;
}

.x-turn-hover::before,
.x-turn-hover::after {
  content: '';
  position: absolute;
  width: 70px;
  height: 4px;
  background-color: #173f5f4d;
}

.x-turn-hover::before {
  transform: rotate(45deg);
}

.x-turn-hover::after {
  transform: rotate(-45deg);
}

.o-turn-hover::before {
  content: '';
  position: absolute;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  border: 4px solid #ed563b3a;
}

.display-winner {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 6em 0;
  animation-name: result;
  animation-duration: 1s;
}

.display-winner h2,
.display-winner button {
  margin: 0 auto;
  font-size: 1.6rem;
}

.display-winner h2 {
  padding: 1.5em 2em;
}

.display-winner button {
  margin-top: -0.15em;
  padding: 1.2em 0.6em;
  cursor: pointer;
  background: var(--white);
  color: var(--blue-bg);
  border: 0;
  outline: none;
  transition: 0.3s ease;
}

.display-winner button:hover {
  color: var(--red-bg);
  box-shadow: 0px 0px 10px var(--red-bg);
}

.hide {
  display: none;
}

@keyframes result {
  from {
    transform: rotateX(90deg);
  }
  to {
    transform: rotateX(0);
  }
}